<template>
  <!--  : === v-bind 绑定data的属性或者外部组件prop的属性-->
  <span :title="username">{{ slug }}</span>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';

export default {
  name: 'Avatar',
  data() {
    return {};
  },
  created() {
    this.setUser();
  },
  methods: {
    ...mapActions(
      {setUser: 'checkLogin'}
    )
  },
  computed: {
    ...mapGetters([
      'username',
      'slug'
    ]),
    slug() {
      //从一个字符串中返回一个字符串
      return this.username.charAt(0);
    }
  }
};
</script>

<style scoped>
span {
  display: inline-block;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 32px;
  border-radius: 50%;
  background: #f2b81c;
  color: #fff;
  text-shadow: 1px 0 1px #795c19;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 18px;
  margin-top: 15px;
}
</style>



