<template>
  <div id="sidebar">
    <Avatar/>
    <div class="icons">
      <router-link to="/notebooks" title="笔记本">
        <img src="../assets/images/notebook.png" alt="notebook">
      </router-link>
      <router-link to="/note" title="笔记详情">
        <img src="../assets/images/list.png" alt="list">
      </router-link>
      <router-link to="/trash" title="回收站">
        <img src="../assets/images/trash.png" alt="trash">
      </router-link>
    </div>
    <div class="logout">
      <img src="../assets/images/logout.png" alt="logout" @click="onLogout">
    </div>
  </div>
</template>

<script>
import Avatar from './Avatar';
import Auth from '../apis/auth';
import {mapActions,} from 'vuex';

export default {
  name: 'Sidebar',
  components: {
    Avatar
  },

  methods: {
    ...mapActions([
      'logout'
    ]),
    onLogout() {
      this.logout({path: 'login'});
    }
  }

};
</script>

<style lang="less" scoped>

#sidebar {
  width: 56px;
  text-align: center;
  background: #2c333c;
  position: relative;


  .icons {
    margin-top: 15px;

    a {
      padding: 6px 0;
      display: block;
    }

    .router-link-active {
      background-color: #5e6266;
    }

    img {
      width: 26px;
    }
  }

  .logout {
    bottom: 16px;
    width: 100%;
    text-align: center;
    cursor: pointer;
    position: absolute;

    img {
      width: 26px;
    }
  }

  .iconfont {
    color: #fff;
  }
}
</style>



