<template>
  <div id="welcome">
    <h1 class="wel">{{ msg }}</h1>
    <div class="button">
      <el-button type='success' @click="open">开始使用</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'welcome',
  data() {
    return {
      msg: '欢迎来到落雨笔记'
    };
  },
  methods: {
    open() {
      this.$router.replace('/login');
    }
  }
};
</script>

<style lang="less" scoped>
#welcome {
  border: 1px solid green;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;


  ul, ol {
    list-style: none;
  }

  .wel {
    font-size: 60px;
    text-shadow: 4px 10px 20px grey;

  }

  .button {
    margin-top: 10%;
    box-shadow: 6px 5px 24px #666666;
    border-radius:10px ;
    color: #fafafa;
  }
}
</style>



